* {
    box-sizing: border-box;
}

.header {
    position: absolute;
    height: 0px;
    top: 40px;
    min-width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: 0 auto;
    z-index: 10;

}

.fixed {
    height: 62px;
    min-width: 300px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-width: 100%;
    margin: 0 auto;
    z-index: 10;
    position: fixed;
    transition: height 0.2s ease-out;
    top: 0;
    background-color: #ffffff;
    box-shadow: 0 4px 15px -5px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
}

nav {
    width: 400px;
    min-width: 298px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    z-index: 2;
}

.backgroundPhoto {
    max-width: 100%;
    /*height: 100vh;*/
    background-image: url("https://pbs.twimg.com/media/DcT2RhEU8AIFvXu?format=jpg&name=4096x4096");
    min-width: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% -25px;
}

.backgroundPhoto:before {
    position: absolute;
    content: "";
    left: 0;
    bottom: 0;
    right: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.4);
    min-width: 300px;
}

.welcomeContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    flex-direction: column;
}

.welcomeBlock {
    text-align: center;
}

.welcomeBlock h1 {
    z-index: 1;
    font-size: 50px;
    color: #fff;
    font-weight: 900;
    line-height: 1;
}

.welcomeBlock p {
    z-index: 1;
    color: #fff;
    font-size: 1.2rem;
}

.welcomeBlock p a {
    z-index: 1;
    color: #96bb7c;
    font-weight: 700;
    font-size: 1.2rem;
}


@media screen and (max-width: 768px) {
    .headerContainer {
        justify-content: center !important;
    }

    .welcomeBlock h1 {
        font-size: 30px;
    }

